<template>
    <div class="address-list">
        <van-nav-bar title="地址管理" left-text="返回" left-arrow @click-left="back"/>
        <Bg>
            <van-address-list
            :list="list"
            default-tag-text="默认"
            :switchable="false"
            @add="add"
            @edit="edit"
            />
        </Bg>
    </div>
</template>

<script>
    import '../assets/less/addresslist.less'
    import Bg from '../components/Bg'
    export default {
        name:'AddressList',
        data() {
            return {
                list: []
            };
        },
        created(){
            this.getAddressListData();
        },
        methods:{
            //返回
            back(){
                this.$router.push({name:"My"});
            },
            //新增地址
            add(){
                this.$router.push({name:'Address'});
            },
            //编辑
            edit({aid}){
                this.$router.push({name:'Address',params:{aid}});
            },
            //查询用户地址列表
            getAddressListData(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录界面
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '保存中',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAddress',
                    params:{
                        appkey:this.appkey,
                        tokenString,
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，//直接退出
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:2000,
                        })
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 20000){
                        //添加address属性
                        res.data.result.forEach(v => {
                            v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;
                            v.isDefault = Boolean(v.isDefault);
                        });
                        if(!res.data.result){
                            return ;
                        }
                        this.list = res.data.result;
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            }
        },
        components:{
            Bg
        }
    }
</script>
